import React, { useState, useEffect } from "react";
import "./styles.css";
import { Form, Select, DatePicker, Input, Button } from "antd";
import { Col, Popconfirm, message } from "antd";
import TextArea from "antd/lib/input/TextArea";
import { hideModal, updateState } from "../../../actions/index";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

const CreateTaskEmployee = ({
  dispatch,
  text,
  history,
  form: { getFieldDecorator }
}) => {
  let addTask = { ...text };
  addTask.index = "CVNV7";
  addTask.departmentCode = text.index;
  addTask.worker = "V Vn Nam";
  addTask.weight = "0.15";
  addTask.status = 0;

  const { Option } = Select;
  const [selectedTypeTask, setSelectedTypeTask] = useState("0");
  const [stateInput, setStateInput] = useState({});
  const [stateSelect, setStateSelect] = useState([]);
  const [isSubmitted, setIsSubmitted] = useState(false);

  useEffect(() => {
    setSelectedTypeTask("0");
    return () => {
      setSelectedTypeTask("0");
    };
  }, [text]);

  const [messageCheckDevice, setMessageCheckDevice] = useState(
    "C y  thit b khng? Cc thit b cn : M s thit b, tem"
  );
  const [messageCheckVeSinh, setMessageCheckVeSinh] = useState([
    "Kim tra trang phc: Qun o, m, gang tay, giy dp",
    "Kim tra v sinh khu vc sn xut",
    "Kim tra cht st trng v ty ra"
  ]);

  const [messageCheckProduction, setMessageCheckProduction] = useState(
    "Kim tra vic p dng quy trnh sn xut"
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 }
    }
  };
  const children = [];

  children.push(<Option key="V Duy Mnh">DBCL_01 - V Duy Mnh</Option>);
  children.push(<Option key="L nh Phc">DBCL_02 - L nh Phc</Option>);
  children.push(<Option key="V Vn Nam">DBCL_03 - V Vn Nam</Option>);
  children.push(<Option key="L Minh Nguyn">DBCL_04 - L Minh Nguyn</Option>);
  children.push(
    <Option key="G. Nguyn Vit Hng">G. Nguyn Vit Hng</Option>
  );

  function handleChangeInput({ target: { name, value } }) {
    setStateInput({ ...stateInput, [name]: value });
  }

  function handleChangeSelectX(value) {
    setStateSelect([...stateSelect, value]);
  }

  function handleChangeCheckDevice(value) {
    setMessageCheckDevice(value.target.value);
  }

  function handleChangeCheckVeSinh(value) {
    setMessageCheckVeSinh(value.target.value);
  }

  function handleChangeCheckProduction(value) {
    setMessageCheckProduction(value.target.value);
  }

  console.log("stateINput:", stateInput);
  console.log("stateSelect:", stateSelect);

  return (
    <div className="create-task-employee">
      <Form {...formItemLayout}>
        <Col xl={24} lg={24} style={{ margin: "0 auto" }}>
          <Form.Item label="M cng vic phng ban">
            <Input value={text.index} disabled />
          </Form.Item>
          <Form.Item label="Tn cng vic">
            <Input
              placeholder=""
              required
              onChange={handleChangeInput}
              name="name"
            />
          </Form.Item>
          <Form.Item label="Ngi giao vic">
            <Input placeholder="" disabled={true} value="V Duy Mnh" />
          </Form.Item>
          <Form.Item label="Chc v">
            <Input
              placeholder=""
              disabled={true}
              value="Trng phng b phn m bo cht lng"
            />
          </Form.Item>
          <Form.Item label={`Trng s (cn li ${text.remainWeight})`}>
            <Input
              placeholder={`0.0 <= Trng s  <= ${text.remainWeight}`}
              onChange={handleChangeInput}
              name="weight"
            />
          </Form.Item>
          <Form.Item label="Loi cng vic">
            <Input value={text.type} disabled></Input>
          </Form.Item>

          <Form.Item label="Ngi ph trch">
            <Select
              mode="tags"
              style={{ width: "100%" }}
              placeholder="Chn ngi ph trch"
              name="worker"
              onChange={handleChangeSelectX}
            >
              {children}
            </Select>
          </Form.Item>
          <Form.Item label="Ngi kim duyt">
            <Select
              mode="tags"
              style={{ width: "100%" }}
              placeholder="Chn ngi kim duyt"
              name="censor"
              onChange={handleChangeSelectX}
            >
              {children}
            </Select>
          </Form.Item>

          {text.type !== "Kim tra nguyn liu u vo" && (
            <Form.Item label="Nhm quy trnh">
              <Select value={selectedTypeTask} onChange={handleChangeSelectX}>
                <Option value="0">Nhm quy trnh...</Option>
                <Option value="1">
                  {text.type === "Kim tra quy trnh sn xut"
                    ? "KTCL quy trnh xng thuc nc th y"
                    : "KTCL bao b cp 1"}
                </Option>
                <Option value="2">
                  {text.type === "Kim tra quy trnh sn xut"
                    ? "KTCL quy trnh xng thuc bt th y"
                    : "KTCL bao b cp 2"}
                </Option>
                {text.type === "Kim tra quy trnh sn xut" && (
                  <Option value="3">KTCL quy trnh xng TPCN</Option>
                )}
              </Select>
            </Form.Item>
          )}

          {selectedTypeTask !== "0" &&
          text.type === "Kim tra quy trnh sn xut" ? (
            <>
              <Form.Item label="S ln kim tra nh k 1 thng">
                <Input placeholder="" />
              </Form.Item>
              <Form.Item label="S ln kim tra t xut 1 thng">
                <Input placeholder="" />
              </Form.Item>
              <Form.Item label="Kim tra thit b sn xut">
                <TextArea
                  rows={4}
                  value={messageCheckDevice}
                  onChange={handleChangeCheckDevice}
                ></TextArea>
              </Form.Item>
              <Form.Item label="Kim tra v sinh">
                <TextArea
                  rows={4}
                  value={messageCheckVeSinh.map((item, id) => {
                    if (id === 0) {
                      return item;
                    }
                    return "\n" + item;
                  })}
                  onChange={handleChangeCheckVeSinh}
                ></TextArea>
              </Form.Item>
              <Form.Item label="Kim tra quy trnh sn xut">
                <TextArea
                  rows={4}
                  value={messageCheckProduction}
                  onChange={handleChangeCheckProduction}
                ></TextArea>
              </Form.Item>
            </>
          ) : null}

          {selectedTypeTask !== "0" &&
          text.type === "Kim tra sn phm u ra" ? (
            <>
              <Form.Item label="S bao b sn phm cn kim tra">
                <Input placeholder="" />
              </Form.Item>
            </>
          ) : null}
          <Form.Item label="Ngy ht hn" style={{ marginBottom: 0 }}>
            <Form.Item
              style={{ display: "inline-block", width: "calc(50% - 12px)" }}
            >
              <DatePicker />
            </Form.Item>
          </Form.Item>
        </Col>
        <Col xl={24} lg={24} style={{ float: "right" }}>
          <Link to="/list-task-employee">
            <Button
              type="primary"
              style={{
                display: "block",
                marginRight: "180px",
                float: "right"
              }}
              htmlType="submit"
              onClick={() => {
                dispatch(updateState({ addTask }));
                message.success("Bn  to cng vic thnh cng !");
                dispatch(hideModal());
              }}
              disabled={
                Object.keys(stateInput).length === 2 && stateSelect.length >= 3
                  ? false
                  : true
              }
            >
              To mi
            </Button>
          </Link>

          <Button
            type="danger"
            style={{ float: "right", marginRight: "10px" }}
            onClick={() => {
              setSelectedTypeTask(0);
              dispatch(hideModal());
            }}
          >
            Hu
          </Button>
        </Col>
      </Form>
    </div>
  );
};

export default connect()(
  Form.create({ name: "create-task" })(CreateTaskEmployee)
);
